@use "abstract" as *;
@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,400;0,600;1,700&display=swap");

// box sizing
*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: var(--scroll-padding, rem(50));
}

// set up body
body {
  font-family: $ff-sans-serif;
  min-height: 100vh;
  font-size: 14px;
  line-height: $lh-base;
  background-color: $black;
}

// reset margins
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
figure,
picture {
  margin-top: 0;
}

// text balancing
h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

// images
img,
picture {
  max-width: 100%;
  height: auto;
}

// list
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

// links

a,
a:visited,
a:active {
  cursor: pointer;
  text-decoration: none;
}

// buttons
button {
  cursor: pointer;
  font: inherit;
}

// forms
input,
textarea {
  font-family: inherit;
}

/* remove animations for people who turned it off*/
@media (prefers-reduced-motion: reduce) {
  *,
  *::after,
  *::before {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
