.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  padding: 1.875rem 0 !important;
}
.nav .nav__brand .nav__brand-image {
  height: 2.1875rem;
}
@media (min-width: 48rem) {
  .nav .nav__brand .nav__brand-image {
    height: 2.5rem;
  }
}
.nav .nav__toggle {
  cursor: pointer;
  height: 1.5rem;
  width: 1.5rem;
  position: relative;
  transition: background-color 1s ease-in-out;
}
.nav .nav__toggle:hover .nav__burger-line {
  background-color: #e7e7eb;
}
.nav .nav__toggle .nav__burger-line {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 0.15rem;
  border-radius: 0.3125rem;
  background-color: #d83a34;
  transition: transform 0.5s, width 0.5s;
}
.nav .nav__toggle .nav__burger-line--1 {
  transform: translate(-50%, -7px);
}
.nav .nav__toggle .nav__burger-line--2 {
  width: 100%;
}
.nav .nav__toggle .nav__burger-line--3 {
  transform: translate(-50%, 5px);
}
.nav .nav__links {
  visibility: hidden;
  height: 0;
  position: absolute;
}
@media (min-width: 48rem) {
  .nav .nav__links {
    visibility: visible;
    position: relative;
    width: 470px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    background-color: #2c2c2c;
    margin-top: 0.625rem;
  }
}
@media (min-width: 62rem) {
  .nav .nav__links {
    display: none;
  }
}
.nav .nav__links .nav__lists {
  display: flex;
  width: 420px;
  flex-direction: row;
}
.nav .nav__links .nav__lists .nav__list {
  position: relative;
  width: 4.375rem;
  height: 4.375rem;
  z-index: 1;
}
.nav .nav__links .nav__lists .nav__list:nth-child(1).nav__list--active ~ .nav__indicator {
  transform: translateX(0px);
}
.nav .nav__links .nav__lists .nav__list:nth-child(2).nav__list--active ~ .nav__indicator {
  transform: translateX(70px);
}
.nav .nav__links .nav__lists .nav__list:nth-child(3).nav__list--active ~ .nav__indicator {
  transform: translateX(140px);
}
.nav .nav__links .nav__lists .nav__list:nth-child(4).nav__list--active ~ .nav__indicator {
  transform: translateX(210px);
}
.nav .nav__links .nav__lists .nav__list:nth-child(5).nav__list--active ~ .nav__indicator {
  transform: translateX(280px);
}
.nav .nav__links .nav__lists .nav__list:nth-child(6).nav__list--active ~ .nav__indicator {
  transform: translateX(350px);
}
.nav .nav__links .nav__lists .nav__list .nav__link {
  position: relative;
  display: flex;
  align-items: center;
  text-align: center;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  font-weight: 500;
}
.nav .nav__links .nav__lists .nav__list .nav__link .nav__link-icon {
  position: relative;
  display: block;
  line-height: 4.6875rem;
  font-size: 1.5em;
  color: #d83a34;
}
@media (min-width: 48rem) {
  .nav .nav__links .nav__lists .nav__list .nav__link .nav__link-icon {
    transition: 0.5s;
  }
}
.nav .nav__links .nav__lists .nav__list .nav__link .nav__link-text {
  position: absolute;
  padding: 0.0625rem 0.4375rem;
  background-color: #d83a34;
  color: #e7e7eb;
  border-radius: 12px;
  font-weight: 400;
  font-size: 0.75em;
  letter-spacing: 0.05em;
  opacity: 0;
  transform: translateY(20px);
}
@media (min-width: 48rem) {
  .nav .nav__links .nav__lists .nav__list .nav__link .nav__link-text {
    transition: 0.5s;
  }
}
.nav .nav__links .nav__lists .nav__list--active .nav__link .nav__link-icon {
  transform: translateY(-15px);
  opacity: 1;
  color: #e7e7eb;
}
.nav .nav__links .nav__lists .nav__list--active .nav__link .nav__link-text {
  transform: translateY(20px);
  opacity: 1;
}
.nav .nav__links .nav__indicator {
  position: absolute;
  top: -20%;
  width: 3.75rem;
  height: 3.75rem;
  background-color: #d83a34;
  border-radius: 50%;
  border: 6px solid #2c2c2c;
  left: 1.875rem;
}
@media (min-width: 48rem) {
  .nav .nav__links .nav__indicator {
    transition: 0.5s;
  }
}
.nav .nav__cta {
  letter-spacing: 1.5px;
  display: none;
}
@media (min-width: 62rem) {
  .nav .nav__cta {
    display: inline-block;
  }
}

.nav__list:not(.nav__list--active):hover .nav__link-icon {
  transform: translateY(-32px);
  opacity: 1;
}
@media (min-width: 48rem) {
  .nav__list:not(.nav__list--active):hover .nav__link-icon {
    opacity: 0 !important;
  }
}
.nav__list:not(.nav__list--active):hover .nav__link-text {
  transform: translateY(0px) !important;
  opacity: 1 !important;
}

.nav--open-menu {
  padding: 20px 0;
}
.nav--open-menu .nav__toggle .nav__burger-line--1 {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.nav--open-menu .nav__toggle .nav__burger-line--2 {
  width: 0;
}
.nav--open-menu .nav__toggle .nav__burger-line--3 {
  transform: translate(-50%, -50%) rotate(45deg);
}
.nav--open-menu .nav__links {
  visibility: visible;
  height: auto;
  display: flex;
  flex-flow: column;
  position: absolute;
  right: 50%;
  transform: translateX(50%);
  top: 4.375rem;
  padding: 1.25rem;
  border-radius: 5px;
  align-items: center;
  z-index: 3;
  width: 100%;
  animation: opac 0.5s linear;
  background-color: #2c2c2c;
}
.nav--open-menu .nav__links .nav__lists {
  width: 100%;
  justify-content: space-around;
}
.nav--open-menu .nav__links .nav__lists .nav__list {
  height: 3.4375rem;
  width: 3.4375rem;
}
.nav--open-menu .nav__links .nav__lists .nav__list--active .nav__link-text {
  transform: translateY(10px) !important;
}
.nav--open-menu .nav__links .nav__lists .nav__list--active .nav__link-icon {
  transform: translateY(-28px) !important;
  background: #d83a34 !important;
  display: flex !important;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: 4px solid #2c2c2c;
  width: 3.4375rem;
  height: 3.4375rem;
}
.nav--open-menu .nav__links .nav__lists .nav__indicator {
  display: none !important;
}

.nav__brand--shrink {
  padding: 0.625rem 0 !important;
}
.nav__brand--shrink .nav__brand .nav__brand-image {
  height: 1.875rem;
}
.nav__brand--shrink .nav__brand .nav__brand-name {
  font-size: 1rem;
}

