.header {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 1001;
}
.header .overlay {
  position: fixed;
  z-index: 2;
  left: 0;
  right: 0;
  bottom: 0;
  top: 3.625rem;
  background-image: linear-gradient(to bottom, #e88d8a, transparent);
  cursor: pointer;
}

.hero {
  min-height: 41.75rem;
  min-height: 100dvh;
  height: 100%;
  display: flex;
  justify-content: flex-end;
  background: url(../assets/images/hero-section/background-stars.svg);
  background-color: #2c2c2c;
  align-items: center;
}
.hero .hero-cta {
  display: flex;
  justify-content: center;
}
@media (min-width: 62rem) {
  .hero .hero-cta {
    justify-content: start;
  }
}
.hero .hero__image--md {
  margin-top: 1.25rem;
}
@media (min-width: 48rem) {
  .hero .hero__image--md {
    margin-top: 7.5rem;
  }
}
@media (min-width: 62rem) {
  .hero .hero__image--md {
    display: none;
  }
}
.hero .hero__image--md img {
  max-width: 12.5rem;
  max-height: 12.5rem;
  border: 3px solid #d83a34;
  filter: drop-shadow(-2px 8px 11px #d83a34);
  background: #2c2c2c;
  margin-bottom: 0.625rem;
}
.hero .hero__text {
  text-align: center;
}
@media (min-width: 62rem) {
  .hero .hero__text {
    text-align: left;
  }
}
.hero #hero-menu-container {
  display: none;
}
@media (min-width: 62rem) {
  .hero #hero-menu-container {
    display: flex;
  }
}
.hero .hero__image {
  max-width: 18.75rem;
  border-radius: 50%;
  filter: drop-shadow(-2px 8px 11px #d83a34);
}
.hero .hero-cta__about {
  transition: background-color 0.5s ease-out;
}
.hero .hero-cta__about:hover {
  background-color: rgba(216, 58, 52, 0.5);
}
.hero .hero-cta__about .hero-cta__hire-arrows {
  position: relative;
  width: 3rem;
  height: 3rem;
}
.hero .hero-cta__about .hero-cta__hire-arrows .arrow {
  height: 0.2rem;
  opacity: 0;
  position: absolute;
  transform: scale(0.1);
  width: 1.2rem;
  left: 25%;
}
.hero .hero-cta__about .hero-cta__hire-arrows .arrow:nth-child(1) {
  animation: move-arrow 2s linear 1s infinite;
}
.hero .hero-cta__about .hero-cta__hire-arrows .arrow:nth-child(2) {
  animation: move-arrow 2s linear 2s infinite;
}
.hero .hero-cta__about .hero-cta__hire-arrows .arrow:nth-child(3) {
  animation: move-arrow 2s linear 3s infinite;
}
.hero .hero-cta__about .hero-cta__hire-arrows .arrow:before,
.hero .hero-cta__about .hero-cta__hire-arrows .arrow:after {
  content: "";
  position: absolute;
  top: 0;
  height: 100%;
  width: 50%;
  background: #e1e1e1;
}
.hero .hero-cta__about .hero-cta__hire-arrows .arrow:before {
  left: 0;
  transform: skewY(30deg);
}
.hero .hero-cta__about .hero-cta__hire-arrows .arrow:after {
  right: 0;
  width: 50%;
  transform: skewY(-30deg);
}
@keyframes move-arrow {
  25% {
    opacity: 1;
  }
  33.3% {
    opacity: 1;
    transform: translateY(2rem);
  }
  100% {
    opacity: 0;
    transform: translateY(2rem) scale(0.1);
  }
}

