  background-image: conic-gradient(from 90deg at top var(--b) left var(--b), rgba(0, 0, 0, 0) 25%, #d83a34 0), conic-gradient(from -90deg at bottom var(--b) right var(--b), rgba(0, 0, 0, 0) 25%, #d83a34 0);
  background-position: var(--_p, 0%) var(--_p, 0%), calc(100% - var(--_p, 0%)) calc(100% - var(--_p, 0%));
  background-size: var(--s) var(--s);
